.starability-result {
  color: #0000;
  width: 150px;
  font-size: .1em;
  position: relative;
}

.starability-result, .starability-result:after {
  background-image: url("data:image/png;base64,");
  height: 30px;
}

.starability-result:after {
  content: " ";
  background-position: 0 -30px;
  position: absolute;
  left: 0;
}

.starability-result[data-rating="5"]:after {
  width: 150px;
}

.starability-result[data-rating="4"]:after {
  width: 120px;
}

.starability-result[data-rating="3"]:after {
  width: 90px;
}

.starability-result[data-rating="2"]:after {
  width: 60px;
}

.starability-result[data-rating="1"]:after {
  width: 30px;
}

@media screen and (-webkit-device-pixel-ratio >= 2), screen and (resolution >= 192dpi) {
  .starability-result, .starability-result:after {
    background-image: url("data:image/png;base64,");
    background-size: 30px;
  }
}

@keyframes a {
  0% {
    transform: scale(1);
  }

  70% {
    opacity: 0;
    transform: scale(3);
  }

  to {
    opacity: 0;
    transform: scale(1);
  }
}

.starability-grow {
  will-change: contents;
  border: none;
  width: 150px;
  min-height: 60px;
  padding: 0;
  display: block;
  position: relative;
}

.starability-grow > input {
  opacity: 0;
  margin-right: -100%;
  position: absolute;
}

.starability-grow > input:checked ~ label, .starability-grow > input:focus ~ label {
  background-position: 0 0;
}

.starability-grow > input:checked + label, .starability-grow > input:focus + label {
  background-position: 0 -30px;
}

.starability-grow > input[disabled]:hover + label {
  cursor: default;
}

.starability-grow > input:not([disabled]):hover ~ label {
  background-position: 0 0;
}

.starability-grow > input:not([disabled]):hover + label {
  background-position: 0 -30px;
}

.starability-grow > input:not([disabled]):hover + label:before {
  opacity: 1;
}

.starability-grow > input:focus + label {
  outline: 1px dotted #999;
}

.starability-grow .starability-focus-ring {
  pointer-events: none;
  opacity: 0;
  outline: 2px dotted #999;
  width: 100%;
  height: 30px;
  position: absolute;
  left: 0;
}

.starability-grow > .input-no-rate:focus ~ .starability-focus-ring {
  opacity: 1;
}

.starability-grow > label {
  float: left;
  color: #0000;
  cursor: pointer;
  background-position: 0 -30px;
  background-repeat: no-repeat;
  width: 30px;
  font-size: .1em;
  display: inline-block;
  position: relative;
}

.starability-grow > label, .starability-grow > label:before {
  background-image: url("data:image/png;base64,");
  height: 30px;
}

.starability-grow > label:before {
  content: "";
  pointer-events: none;
  opacity: 0;
  background-position: 0 30px;
  display: block;
  position: absolute;
}

.starability-grow > label:nth-of-type(5):before {
  width: 120px;
  left: -120px;
}

.starability-grow > label:nth-of-type(4):before {
  width: 90px;
  left: -90px;
}

.starability-grow > label:nth-of-type(3):before {
  width: 60px;
  left: -60px;
}

.starability-grow > label:nth-of-type(2):before {
  width: 30px;
  left: -30px;
}

.starability-grow > label:first-of-type:before {
  width: 0;
  left: 0;
}

@media screen and (-webkit-device-pixel-ratio >= 2), screen and (resolution >= 192dpi) {
  .starability-grow > label {
    background-image: url("data:image/png;base64,");
    background-size: 30px;
  }
}

@media screen and (-ms-high-contrast: active) {
  .starability-grow {
    width: auto;
